.navbar {
  /* background: rgba(27, 64, 165, 0.295); */
  background: #4267b2;
  height: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  /* font-size: 1.2rem; */
  position: sticky;
  top: -10px;
  z-index: 999;
}

.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100px;
  max-width: 1500px;
}

.navbar-logo {
  color: #fff;
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  flex-direction: column;
  font-weight: 700;
  font-size: 30px;
  display: flex;
  align-items: center;
}
.tagline {
  font-size: 10px;
}
.fa-typo3 {
  margin-left: 0.5rem;
  font-size: 1.8rem;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(7, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 60vw;
  justify-content: end;
  margin-right: 2rem;
}

.nav-item {
  height: 80px;
}

.nav-links {
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
}

.nav-links:hover {
  border-bottom: 4px solid #fff;
  transition: all 0.2s ease-out;
}

.fa-bars {
  color: #fff;
  margin-top: 10px;
  size: 10px;
}

.nav-links-mobile {
  display: none;
}
.header__optionBasket {
  display: flex;
  align-items: center;
  color: white;
}
.menu-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: block;
    block-size: auto;
    flex-direction: column;
    width: 100%;
    align-items: center;
    height: 70vh;
    position: absolute;
    box-shadow: 0px 0px 15px -5px;
    top: 80px;
    margin-left: 0px;
    margin-right: 20px;
    margin-top: 40px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
    border-radius: 20px;
  }

  .nav-menu.active {
    background: rgb(66, 103, 178);
    left: 0;
    opacity: 1;
    transition: all 0.5s ease;
    z-index: 1;
  }

  .nav-links {
    text-align: center;
    align-items: center;
    /* padding: 1rem; */
    width: 40%;
    display: table;
    margin-left: 100px;
  }

  .nav-links:hover {
    background-color: #fff;
    color: #242424;
    border-radius: 10px;
  }

  .navbar-logo {
    position: absolute;
    top: 0;
    left: 0;
    transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }

  .fa-times {
    color: #fff;
    font-size: 2rem;
  }

  .nav-links-mobile {
    display: block;
    text-align: center;
    margin: 2rem auto;
    border-radius: 4px;
    width: 80%;
    text-decoration: none;
    font-size: 1.5rem;
    background-color: transparent;
    color: #fff;
    padding: 14px 20px;
    border: 1px solid #fff;
    transition: all 0.3s ease-out;
  }

  .nav-links-mobile:hover {
    background: #fff;
    color: #242424;
    transition: 250ms;
  }
}
