.card-container {
  margin: 30px 20px 40px 100px;
  width: 300px;
  overflow: hidden;
  box-shadow: 0px 0px 15px -5px;
  transition: 0.5s;
  animation: ease-in-out;
  border-radius: 10px;
  border-color: rgb(35, 155, 211);
  border-style: solid;
}

.card-container:hover {
  transform: scale(1.1);
  box-shadow: 0px 0px 15px 0px;
}

.image-container img {
  overflow: hidden;
  height: 200px;
}

.card-content {
  margin: 1rem;
  margin-top: 0.5rem;
}

h3,
p {
  margin: 0;
  padding: 0;
}

.card-title {
  margin-top: 1rem;
  margin-left: 1.2rem;

  font-size: 30px;
  font-family: "Courier New", Courier, monospace;
}

.card-body {
  margin-top: -1.2rem;
}
